.hustle-modal:not(.hustle-modal-image_only) .hustle-modal-image {

	img {
		width: auto;
		height: auto;
		max-width: 100%;
		max-height: 100%;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}
}

.hustle-modal.hustle-modal-simple {

	.hustle-modal-body {
		overflow: auto;
	}
	
	section {
		flex-direction: row;
	}
}

